import React from 'react'
import { CCard, CRow } from "@coreui/react-pro";

const Profile = () => {
  const results=JSON.parse(localStorage.getItem("user"));
  console.log(results);
  return (
    <>
    <CCard>
     Name: {results.name} <br></br>
     Role:{results.role}
     </CCard>
    </>
  );
};

export default Profile
